.CormorantInfant-serif {
  font-optical-sizing: auto;
  font-weight: 200;
  font-style: normal;
  font-variation-settings: "wdth" 100;
  font-family: "Cormorant Infant", serif;
}

.invisible-scrollbar {
  overflow: hidden;
  display: block;

}
.invisible-scrollbar::-webkit-scrollbar {
  display: none;
}
